#velocityOverlay {
  @import "velocity.import.less";

  z-index: 9000;

  // Kick it old school.
  box-sizing: content-box;
  * {
    box-sizing: content-box;
  }

  position: fixed;
  top: 0;
  right: -780px;
  width: 580px;
  height: 100vh;
  padding: 0 50px 0;
  border-left: @velocity-border;
  background: #fff;
  color: #222;
  font-family: @velocity-font;
  font-weight: 300;
  font-size: 14px;
  text-align: center;
  overflow-y: auto;
  transition: right 0.25s ease-in-out;

  &.visible {
    right: 0;
  }

  h1, h2, h3 {
    font-family: @velocity-font;
    color: #222;
  }

  h3 {
    font-weight: 600;
    letter-spacing: 1px;
  }

  .velocity-btn-close {
    border: 0 none;
    background: transparent;
    display: inline-block;
    width: 50px;
    height: 50px;
    position: absolute;
    top: 4px;
    right: 4px;
    color: @velocity-grey;
    opacity: 0.1;
    font-size: 40px;
    transition: opacity 0.25s ease-in-out;
    &:before {
      content: 'âœ–';
    }
    &:after {
      display: inline-block;
      vertical-align: middle;
      width: 0;
      height: 0;
    }
    &:hover {
      opacity: 0.6;
    }
  }

  .velocity-logo {
    position: relative;
    color: transparent;
    margin: 20px 0 20px 25px;
    height: 60px;
    background: url("velocity-logo.png") no-repeat center;
    background-size: auto 100%;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

    .velocity-icon-status {
      top: 16px;
      left: 50%;
      margin-left: -38px;
      float: left;
    }
  }

  .velocity-icon-status {
    display: inline-block;
    width: 32px;
    height: 28px;
    background: white;
    border-radius: 33%;
    position: relative;
    font: @velocity-font;
    font-size: 16px;
    text-align: center;
    color: white;

    &:before {
      content: '';
      display: block;
      position: absolute;
      top: 25%;
      left: 25%;
      width: 50%;
      height: 50%;
      background: @velocity-blue-dark;
      border-radius: 100%;
      font-style: normal;
      line-height: 1;
    }
  }
  &.failed, .failed {
    .velocity-icon-status {
      background: @velocity-red;
      &:before {
        content: 'âœ–';
        background: transparent;
      }
    }
  }
  &.passed, .passed {
    .velocity-icon-status {
      background: @velocity-green;
      &:before {
        content: 'âœ”';
        background: transparent;
      }
    }
  }
  &.pending, .pending {
    .velocity-icon-status {
      &:before {
        -webkit-animation: @velocity-anim-throb;
        animation: @velocity-anim-throb;
      }
    }
  }

  .velocity-icon-total {
    display: inline-block;
    min-width: 80px;
    background: white;
    border: 4px solid #8abcef;
    border-radius: 10px;
    padding: 2px 0 8px;
    font-style: normal;
    font-family: @velocity-font;
    font-size: 30px;
    line-height: 1;
    margin: 0 60px 0 50px;

    small {
      font-family: @velocity-font-light;
      font-weight: 400;
      text-transform: uppercase;
      font-size: 10px;
      margin-top: 6px;
      display: block;
      letter-spacing: 4px;
    }
  }
  &.failed .velocity-icon-total {
    border-color: @velocity-red;
  }
  &.passed .velocity-icon-total {
    border-color: @velocity-green;
  }

  .velocity-icon-time {
    display: inline-block;
    width: 50px;
    height: 50px;
    background: transparent url("icon-time.png") no-repeat center;
    background-size: 100%;
    border: solid 5px @velocity-grey;
    border-radius: 100%;
    /* vertical-align: 30px; */
    font-style: normal;
    font-weight: 100;
    font-family: @velocity-font-light;
    font-size: 14px;
    line-height: 51px;
  }

  .velocity-summary-text {
    font-size: 40px;
    font-weight: 300;
    letter-spacing: 1px;
    font-family: @velocity-font;
    color: #222;
    line-height: 2;
    margin: 5px 0;
  }

  .velocity-icon-cog {
    width: 16px;
    height: 16px;
    display: inline-block;
    background: url("velocity_cog.svg") no-repeat center;
    background-size: auto 100%;
  }

  .velocity-options-toggle {
    border: solid 1px #ddd;
    border-radius: 3px;
    background: #efefef;
    opacity: 0.4;
    padding: 5px 5px 4px;
    margin: 0;
    line-height: 1;
    transition: opacity 0.25s ease-in-out;
    &:hover {
      opacity: 0.9;
    }
    &:focus {
      outline: none;
    }
  }

  .velocity-options {
    overflow: hidden;
    transition: margin 0.3s linear, opacity 0.3s ease-in-out, max-height 0.3s linear;
    margin: 20px 0 10px;
    opacity: 1;

  }

  .velocity-report {
    //min-height: 150px;
  }

  .velocity-section-header {
    border-bottom: @velocity-border;
    min-height: 30px;
    margin: 10px 0 30px;
    background: rgb(255, 255, 255);

    .velocity-section-name {

      a {
        //color: @velocity-blue;
        text-decoration: none;
      }

      background: inherit;
      color: #AAA;
      font-family: @velocity-font-light;
      font-size: 20px;
      font-weight: 100;
      letter-spacing: 2px;
      position: relative;
      top: 16px;
      padding: 0 2px 0 10px;
    }
  }

  .velocity-suite {
    border-bottom: solid 1px #efefef;
    padding-bottom: 15px;
    &:last-child {
      border-bottom: 0 none;
      padding-bottom: 0;
    }
  }

  .velocity-report .velocity-icon-status {
    border-radius: 38%;
  }

  .velocity-suite-header {
    font-family: @velocity-font;
    font-weight: 600;
    letter-spacing: 1px;
    margin: 20px 0 5px;
    text-align: left;

    &.passed {
      color: @velocity-green;
    }
    &.failed {
      color: @velocity-red;
    }
  }

  // TODO: simplify! We only show the time symbol on passing suites, for the first suite header
  .velocity-report.passed .velocity-icon-status + .velocity-suite {
    position: relative;
    &:before {
      content: '';
      display: block;
      position: absolute;
      top: -20px;
      right: 7px;
      width: 34px;
      height: 34px;
      background: transparent url("icon-time.png") no-repeat center;
      background-size: 100%;
    }
  }

  .velocity-result-table {
    width: 100%;
    text-align: left;

    .velocity-test-name,
    .velocity-test-time {
      letter-spacing: 1px;
      padding: 5px 0;
    }
    .velocity-test-time {
      text-align: right;
      font-family: @velocity-font-mono;
      white-space: nowrap;
    }
  }

  .velocity-test.failed .velocity-test-time {
    color: @velocity-red;
  }

  .velocity-test.passed .velocity-test-time {
    color: @velocity-green
  }

  .velocity-fail-msg {
    font-size: 20px;
    line-height: 1.5;
    letter-spacing: 1px;
  }

  .velocity-fail-msg .velocity-icon-status {
    width: 20px;
    height: 20px;
    border-width: 0px;
    font-size: 10px;
    color: transparent;
    margin: 0 8px 0 -36px;
  }

  .velocity-stack-trace {
    font-family: @velocity-font-mono;
    font-size: 11px;
    letter-spacing: 1px;
    max-width: 525px;
    overflow-x: auto;
    border-left: solid 3px rgb(34, 34, 34);
    margin: 8px 0 12px -4px;
  }

  .velocity-notice-inline,
  .velocity-stack-trace {
    border-radius: 3px;
    background: rgb(240, 240, 240);
    padding: 20px;
  }

  .btn-velocity {
    background: @velocity-grey-light;
    color: rgb(255, 255, 255);
    border: 0 none;
    border-radius: 3px;
    letter-spacing: 1px;
    padding: 10px 10px;
    font-size: 12px;
    font-family: @velocity-font;
    font-weight: 400;
    opacity: 0.8;
    transition: opacity 0.2s ease-in-out, background 0.2s ease-in-out;

    &:hover {
      opacity: 1;
    }
    &:focus {
      outline: none;
    }
    &.active {
      background: @velocity-green;
    }
  }

  .velocity-iframe {
    display: none;

    iframe {
      width: 100%;
      margin-bottom: 50px;
      min-height: 500px;
      border: @velocity-border;
    }
  }
  .velocity-iframe.visible {
    display: block;
  }

  .velocity-table-box {
    max-width: 100%;
    overflow-x: auto;
    margin: 0 0 50px;
  }

  .velocity-table {
    min-width: 100%;
    background: rgb(240, 240, 240);
    text-align: left;
    border-collapse: collapse;
    white-space: nowrap;
    margin: 0;
    td, th {
      border-bottom: solid 1px white;
      padding: 8px 10px;
    }
    th {
      border-bottom: solid 2px rgb(180, 180, 180)
    }
  }

  .velocity-icon-loading {
    width: 32px;
    height: 32px;
  }

  .velocity-icon-loading {
    display: block;
    margin: 0 auto;
    width: 20px;
    height: 20px;
    background: #4990E2;
    border-radius: 100%;
    -webkit-animation: @velocity-anim-throb;
    animation: @velocity-anim-throb;
  }
}

// TODO: tidy up.
#velocityResetNotification {

  z-index: 10000;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, .7);
  font-size: 2em;

  &:before {
    content: '';
    display: inline-block;
    width: 0;
    height: 100%;
    vertical-align: middle;
  }

  .velocity-notice-inline {
    border: solid 3px rgba(74, 74, 74, 0.1);
    background-color: rgb(255, 255, 255);
    display: inline-block;
    width: 95%;
    max-width: 500px;
  }
}

#velocity-status-widget {
  transition: right 0.25s ease-in-out;
}
